<script>
import axios from 'axios'
import moment from 'moment'
import { mapState } from 'vuex'
export default {
  props: ['tipoPresenca', 'presencaEdit'],
  mounted () {
    console.log(this.tipoPresenca, this.presencaEdit)
    if (this.tipoPresenca === 'editar') {
      console.log('this.presencaEdit', this.presencaEdit)
      this.idPresenca = this.presencaEdit.presenca.idPresenca
      this.dataPresenca = new Date(this.presencaEdit.presenca.dataPresenca).toISOString().substr(0, 10)
      this.matriculaColaborador = this.presencaEdit.presenca.matriculaColaborador
      this.nomeColaborador = this.presencaEdit.presenca.nomeColaborador
      this.funcaoColaborador = this.presencaEdit.presenca.funcaoColaborador
      this.obra = this.presencaEdit.presenca.obra
      this.frenteTrabalho = this.presencaEdit.presenca.frenteTrabalho
      this.regColaborador = this.presencaEdit.presenca.regColaborador
      this.desserts = this.presencaEdit.colaboradores.map(x => {
        return {
          ...x,
          funcao: x.funcao
        }
      })

      console.log(this.desserts)
    }
  },
  data: vm => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    dataHoje: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dataPresenca: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    // dataPresenca: '',
    dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    // dateFormatted: '',
    menu1: false,
    loader: false,
    mensagem: '',
    snackbar: false,
    colorSnackbar: '',
    idPresenca: moment(new Date()).valueOf(),
    // dataCiacao: moment(new Date()).format('L'),
    nomeColaborador: '',
    funcaoColaborador: '',
    matriculaColaborador: 0,
    obra: '',
    frenteTrabalho: '',
    frenteTrabalhos: [],
    loadingFrenteTrabalho: false,
    colaboradores: [],
    camposVerificados: [],
    buscaColaboradores: [],
    selectcolaboradores: [],
    arrayColaboradores: [],
    loadingColaboradores: false,
    regColaborador: null,
    showColaboradores: false,
    // statusPresencas: ['Presente', 'Atestado', 'Falta', 'Baixada', 'Afastamento INSS', 'Treinamento', 'Garagem', 'Demitidos', 'Mobilizao', 'POMPEL - PARAOPEBA', 'Frias', 'Sede'],
    statusPresencas: [],
    headers: [
      {
        text: 'Matricula',
        align: 'start',
        sortable: false,
        value: 'matricula'
      },
      {
        text: 'Nome',
        value: 'nome',
        align: 'center'
      },
      {
        text: 'Funcao',
        value: 'funcao',
        align: 'center'
      },
      {
        text: 'Status',
        value: 'status',
        align: 'center'
      },
      {
        text: 'Repetir',
        value: 'repetir',
        align: 'center'
      },
      {
        text: 'Deletar',
        value: 'deletar',
        align: 'center'
      }
    ],
    desserts: [],
    headersColab: [
      {
        text: 'Matricula',
        align: 'start',
        sortable: false,
        value: 'matricula'
      },
      {
        text: 'Nome',
        value: 'nome',
        align: 'center'
      },
      {
        text: 'Funcao',
        value: 'funcao',
        align: 'center'
      }
    ],
    dessertsColab: [],
    loadingTable: false,
    rules: {
      required: value => !!value || 'Obrigatrio.',
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(value) || 'Invalid e-mail.'
      }
    }
  }),
  async created () {
    await this.getFrenteTrabalhos()
    await this.getColaboradores()
    await this.getStatusPresenca()
    if (this.tipoPresenca !== 'editar') {
      await this.getDadosUser()
    }
  },
  computed: {
    ...mapState('auth', ['user']),
    computedDateFormatted () {
      return this.formatDate(this.dataPresenca)
    }
  },

  watch: {
    dataPresenca (val) {
      this.dateFormatted = this.formatDate(this.dataPresenca)
    }
  },
  methods: {
    formatDate (dataPresenca) {
      if (!dataPresenca) return null

      const [year, month, day] = dataPresenca.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (dataPresenca) {
      if (!dataPresenca) return null

      const [day, month, year] = dataPresenca.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    returnPresenca (time) {
      if (time === undefined) {
        time = '0'
      }
      setTimeout(() => {
        this.$emit('voltar')
      }, time)
    },

    async getColaboradores () {
      this.loadingColaboradores = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}dominio/colaboradores-matriculas`,
        data: {
          obra: this.user.obra ? this.user.obra : null
        }
      }
      axios(options)
        .then(res => {
          this.buscaColaboradores = res.data.map(x => {
            return {
              ...x,
              statusPresenca: ''
            }
          })
          console.log(this.buscaColaboradores)
          this.arrayColaboradores = res.data.map(x => { return x.matricula + ' - ' + x.nome })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar os colaboradores'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingColaboradores = false
        })
    },

    async getStatusPresenca () {
      const options = {
        method: 'GET',
        url: `${this.urlProd}dominio/status-presenca`
      }
      axios(options)
        .then(res => {
          this.statusPresencas = res.data
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar status presena'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
        })
    },

    async getFrenteTrabalhos () {
      this.loadingFrenteTrabalho = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}get-dados-obras`,
        data: {
          obra: this.user.obra ? this.user.obra : null
        }
      }
      axios(options)
        .then(res => {
          this.frenteTrabalhos = res.data.dados
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar as frenteTrabalhos'
          this.colorSnackbar = 'red'
        })
        .finally(() => {
          this.loadingFrenteTrabalho = false
        })
    },

    async getDadosUser () {
      console.log(this.user)
      try {
        this.loader = true
        this.loadingTable = true
        const options = {
          method: 'POST',
          url: `${this.urlProd}get-dados-presenca`,
          data: {
            reg: this.user.reg ? this.user.reg : null
          }
        }
        axios(options)
          .then(res => {
            console.log(res.data)
            this.nomeColaborador = res.data.pramsUser.nome ? res.data.pramsUser.nome : null
            this.funcaoColaborador = res.data.pramsUser.funcao ? res.data.pramsUser.funcao : null
            this.matriculaColaborador = res.data.pramsUser.matricula ? res.data.pramsUser.matricula : null
            this.obra = res.data.pramsUser.obra ? res.data.pramsUser.obra : null
            this.frenteTrabalho = res.data.pramsUser.frenteTrabalho
            console.log('colaboradores', res.data.dados.colaboradores)
            if (res.data.dados.colaboradores) {
              this.desserts = res.data.dados.colaboradores.map(x => {
                return {
                  ...x,
                  statusPresenca: ''
                }
              })
              this.getInfosPresenca()
            } else {
              this.desserts = []
            }
          })
          .catch(err => {
            console.log(err)
            this.snackbar = true
            this.mensagem = 'Erro ao carregar os Dados'
            this.colorSnackbar = 'red'
          })
          .finally(() => {
            this.loader = false
            this.loadingTable = false
          })
      } catch (err) {
        console.log(err)
        this.snackbar = true
        this.mensagem = 'Erro ao carregar os Dados'
        this.colorSnackbar = 'red'
      }
    },

    verificarCampos () {
      this.camposVerificados = []
      if (this.dataPresenca.length < 1) {
        this.camposVerificados.push('Data Presena')
      }

      if (this.frenteTrabalho.length < 1) {
        this.camposVerificados.push('Frente De Trabalho')
      }

      if (this.desserts.length < 1) {
        this.camposVerificados.push('Colaboradores')
      }

      this.salvarPresenca()
    },

    async salvarPresenca () {
      if (this.camposVerificados.length === 0) {
        this.loader = true
        const url = this.tipoPresenca === 'editar' ? `${this.urlProd}editar-presenca` : `${this.urlProd}save-presenca`
        const options = {
          method: 'POST',
          url: url,
          data: {
            idPresenca: this.idPresenca,
            dataPresenca: moment(this.dataPresenca).locale('pt-br').valueOf(),
            nomeColaborador: this.nomeColaborador,
            funcaoColaborador: this.funcaoColaborador,
            matriculaColaborador: this.matriculaColaborador,
            regColaborador: this.user.reg ? this.user.reg : null,
            obra: this.obra,
            frenteTrabalho: this.frenteTrabalho,
            dados: this.desserts
          }
        }
        try {
          axios(options)
            .then(res => {
              if (res.data.colabRepetidos.length === this.desserts.length) {
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'red'
                this.showColaboradores = true
                this.dessertsColab = res.data.colabRepetidos
              } else if (res.data.colabRepetidos.length > 0) {
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'green'
                this.showColaboradores = true
                this.dessertsColab = res.data.colabRepetidos
                this.returnPresenca('3000')
              } else {
                console.log(res)
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'green'
                this.returnPresenca()
              }
            })
            .catch(err => {
              console.log(err)
              this.snackbar = true
              this.mensagem = 'Erro ao salvar!!!'
              this.colorSnackbar = 'red'
            })
            .finally(() => {
              this.loader = false
            })
        } catch (err) {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao salvar!!!'
          this.colorSnackbar = 'red'
          this.loader = false
        }
      } else {
        this.snackbar = true
        this.mensagem = 'Preencher o(s) campo(s): ' + this.camposVerificados.join(', ')
        this.colorSnackbar = 'red'
      }
    },

    /* async editarPresenca () {
      this.loader = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}editar-presenca`,
        data: {
          idPresenca: this.idPresenca,
          dataPresenca: moment(this.dataPresenca).locale('pt-br').valueOf(),
          nomeColaborador: this.nomeColaborador,
          funcaoColaborador: this.funcaoColaborador,
          matriculaColaborador: this.matriculaColaborador,
          regColaborador: this.regColaborador ? this.regColaborador : null,
          obra: this.obra,
          frenteTrabalho: this.frenteTrabalho,
          dados: this.desserts
        }
      }
      try {
        axios(options)
          .then(res => {
            this.snackbar = true
