    copiarInfos (item) {
      console.log(item.statusPresenca)
      this.desserts = this.desserts.map(x => {
        return {
          ...x,
          statusPresenca: item.statusPresenca
        }
      })
      console.log(this.desserts)
    },

    async getInfosPresenca () {
      if (this.dataPresenca.length > 1 && this.frenteTrabalho.length > 1) {
        console.log('getInfosPresenca', this.dataPresenca, this.frenteTrabalho)
        this.loader = true
        const options = {
          method: 'POST',
          url: `${this.urlProd}get-info-presenca`,
          data: {
            dataPresenca: moment(this.dataPresenca).locale('pt-br').valueOf(),
            frenteTrabalho: this.frenteTrabalho
          }
        }
        try {
          axios(options)
            .then(res => {
              if (res.data.result === 400) {
                this.snackbar = true
                this.mensagem = res.data.mensagem
                this.colorSnackbar = 'red'
                this.dataPresenca = ''
              }
            })
            .catch(err => {
              console.log(err)
              this.snackbar = true
              this.mensagem = 'Erro ao salvar!!!'
              this.colorSnackbar = 'red'
            })
            .finally(() => {
              this.loader = false
            })
        } catch (err) {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao salvar!!!'
          this.colorSnackbar = 'red'
          this.loader = false
        }
      }
    },

    adicionarColaboradores () {
      const splitColab = this.selectcolaboradores.map(x => {
        const separador = x.split(' - ')
        return parseInt(separador[0])
      })
      console.log(splitColab)

      for (const item of splitColab) {
        const analisaColab = this.desserts.filter(y => y.matricula === item)
        if (analisaColab.length === 0) {
          const filterColab = this.buscaColaboradores.filter(y => y.matricula === item)
          console.log(filterColab)

          this.desserts = [...this.desserts, ...filterColab]
        } else {
          this.snackbar = true
          this.mensagem = 'Colaborador J Adicionado'
          this.colorSnackbar = 'red'
        }
      }

      this.selectcolaboradores = []
    },

    deletarColaboradores (item) {
      const index = this.desserts.indexOf(item)
      console.log(index)
      if (index > -1) {
        this.desserts.splice(index, 1) // 2nd parameter means remove one item only
      }

      console.log(this.desserts)
    }
  }
}
