                      </v-col>
                    </template>
                  </v-row>
                  <v-row class="px-5">
                    <template>
                      <v-col
                        cols="12"
                        sm="12"
                        md="12"
                        lg="12"
                        xs="12"
                        style="text-align:left"
                      >
                        <v-btn color="blue" class="white--text" @click="moreFilter = !moreFilter">
                          <v-icon>mdi-filter</v-icon> Mais Filtros</v-btn>
                      </v-col>
