                        xs="12"
                      >
                        <template>
                          <v-data-table
                            :headers="headers"
                            :items="desserts"
                            :items-per-page="5"
                            class="elevation-1 color-table"
                            :loading="loadingTable"
                          >
                          <template v-slot:[`item.presenca.statusPresenca`]="{ item }">
                            <v-chip
                              :color="getColor(item.presenca.statusPresenca)"
                              dark
                            >
                              {{ item.presenca.statusPresenca }}
                            </v-chip>
                          </template>
                          <template v-slot:[`item.editar`]="{ item }">
                            <v-btn
                              width="30px"
                              height="30px"
                              fab
                              dark
                              small
                              color="primary"
                              @click="editarPresenca(item)"
                            >
                            <v-icon dark small>
                              mdi-pencil
                            </v-icon>{{ item.editar }}
                            </v-btn>
                          </template>
                          </v-data-table>
