        :presencaEdit="presencaEdit"
      />
    </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import moment from 'moment'
import CriarPresenca from './componentes/CriarPresenca.vue'
export default {
  components: { CriarPresenca },
  name: 'Presenca',
  data: vm => ({
    urlProd: 'https://neexsa.com.br/api_rh/',
    // urlProd: 'http://localhost:4040/api_rh/',
    dataHoje: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dataInicio: (new Date(new Date(new Date().getFullYear(), new Date().getMonth(), 1) - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateFormattedInicio: vm.formatDate((new Date(new Date(new Date().getFullYear(), new Date().getMonth(), 1) - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    menu1: false,
    dataFim: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    dateFormattedFim: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
    menu2: false,
    tipoPresenca: '',
    presencaEdit: [],
    showCriarPresenca: false,
    showPresenca: true,
    snackbar: false,
    mensagem: '',
    colorSnackbar: '',
    textPesquisa: '',
    moreFilter: false,
    matriculaNome: [],
    arrayColaboradores: [],
    loadingColaboradores: false,
    filtroFuncao: [],
    arrayFuncao: [],
    filterFrenteTrabalho: [],
    arrayFrenteTrabalho: [],
    headers: [
      {
        text: 'Data',
        align: 'start',
        sortable: false,
        value: 'dataFormated'
      },
      {
        text: 'ID Presena',
        value: 'presenca.idPresenca',
        align: 'center'
      },
      {
        text: 'Matricula',
        value: 'presenca.matriculaColaborador',
        align: 'center'
      },
      {
        text: 'Nome',
        value: 'presenca.nomeColaborador',
        align: 'center'
      },
      {
        text: 'Funo',
        value: 'presenca.funcaoColaborador',
        align: 'center'
      },
      {
        text: 'Obra',
        value: 'presenca.obra',
        align: 'center'
      },
      {
        text: 'Frente Trabalho',
        value: 'presenca.frenteTrabalho',
        align: 'center'
      },
      {
        text: 'Status',
        value: 'presenca.statusPresenca',
        align: 'center'
      },
      {
        text: 'Editar',
        value: 'editar',
        align: 'center'
      }
    ],
    desserts: [],
    loadingTable: true
  }),
  async created () {
    await this.getPresenca()
  },
  computed: {
    ...mapState('auth', ['user']),
    computedDateFormattedInico () {
      return this.formatDate(this.dataInicio)
    },
    computedDateFormattedFim () {
      return this.formatDate(this.dataFim)
    }
  },
  watch: {
    dataInicio (val) {
      this.dateFormattedInicio = this.formatDate(this.dataInicio)
    },
    dataFim (val) {
      this.dateFormattedFim = this.formatDate(this.dataFim)
    }
  },
  methods: {
    formatDate (data) {
      if (!data) return null

      const [year, month, day] = data.split('-')
      return `${day}/${month}/${year}`
    },
    parseDate (data) {
      if (!data) return null

      const [day, month, year] = data.split('/')
      return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
    },
    returnHome () {
      this.$router.push({ name: 'Home' })
    },
    async getPresenca () {
      this.loadingTable = true
      const options = {
        method: 'POST',
        url: `${this.urlProd}get-presenca`,
        data: {
          textoPesquisar: this.textPesquisa ? this.textPesquisa : '',
          obra: this.user.obra ? this.user.obra : null,
          reg: this.user.reg ? this.user.reg : null,
          dataInicio: this.dataInicio ? moment(this.dataInicio).set({ hour: 0, minute: 0, second: 0, millisecond: 0 }).valueOf() : null,
          dataFim: this.dataFim ? moment(this.dataFim).set({ hour: 23, minute: 59, second: 59, millisecond: 0 }).valueOf() : null,
          matriculaNome: this.matriculaNome ? this.matriculaNome : [],
          filtroFuncao: this.filtroFuncao ? this.filtroFuncao : [],
          filterFrenteTrabalho: this.filterFrenteTrabalho ? this.filterFrenteTrabalho : []
        }
      }
      axios(options)
        .then(res => {
          this.desserts = res.data.map(x => {
            return {
              ...x,
              dataFormated: moment(new Date(x.presenca.dataPresenca)).format('DD/MM/YYYY')
            }
          })
          console.log(res.data)
          this.arrayColaboradores = res.data.map(y => { return y.presenca.matriculaColaborador + ' - ' + y.presenca.nomeColaborador })
          this.arrayFuncao = res.data.map(y => { return y.presenca.funcaoColaborador })
          this.arrayFrenteTrabalho = res.data.map(y => { return y.presenca.frenteTrabalho })
          console.log(this.desserts)
          this.loadingTable = false
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
          this.mensagem = 'Erro ao carregar Dados'
          this.colorSnackbar = 'red'
          this.loadingTable = false
        })
        .finally(() => {
          this.loadingTable = false
        })
    },
    returnPresenca () {
      this.showPresenca = true
      this.showCriarPresenca = false
      this.getPresenca()
    },
    newColaborador () {
      this.showPresenca = false
      this.showCriarPresenca = true
      this.tipoPresenca = 'criar'
    },
    editarPresenca (item) {
      this.presencaEdit = item
      this.tipoPresenca = 'editar'
      this.showPresenca = false
      this.showCriarPresenca = true
    },
    getColor (item) {
      console.log(item)
      if (item === 'Em Andamento') {
        return 'orange'
      } else {
        return 'green'
      }
    }
  }
}
</script>

